<template>
  <div class="register">
  <el-form :model="registerForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="Username" prop="userName" required>
      <el-input prefix-icon="el-icon-user" v-model.number="registerForm.userName"></el-input>
    </el-form-item>
    <el-form-item label="Password" prop="password" required>
      <el-input
          type="password"
          v-model="registerForm.password"
          autocomplete="off"
          prefix-icon="el-icon-lock"
      ></el-input>
    </el-form-item>
    <el-form-item label="Confirm" prop="checkPass" required>
      <el-input type="password" v-model="registerForm.checkPass" autocomplete="off" prefix-icon="el-icon-lock"></el-input>
    </el-form-item>
    <el-form-item label="Phone" prop="userPhone" required>
      <el-input
          prefix-icon="el-icon-mobile-phone" v-model.number="registerForm.userPhone"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="primary" plain @click="register('registerForm')">Submit</el-button>
      <el-button plain @click="resetForm('registerForm')">Reset</el-button>
    </el-form-item>
  </el-form>
  </div>
</template>

<script>
export default {
  name:"Register",
  data() {
    var checkUserName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('Please enter user name!'))
      }
        if (Number.isInteger(value)) {
          callback(new Error('User name can not be numbers!'))
        } else {
          callback()
        }
    }
    var validatePhone =(rule, value, callback) => {
      if(!value) {
        return callback(new Error('Please enter your phone!'))
      }
        if (!Number.isInteger(value)) {
          callback(new Error('Please enter numbers!'))
        } else {
          callback()
        }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Please confirm your password!'));
      } else if (value !== this.registerForm.password) {
        callback(new Error('The two passwords are inconsistent!'));
      } else {
        callback();
      }
    };
    return {
      registerForm: {//表单的数据绑定对象
        password: '',//初始化数据绑定对象的属性
        checkPass: '',
        userName: '',
        userPhone:'',
      },
      rules: {//验证规则对象
        password: [
          { message: 'Please enter password', trigger: 'blur' },
          { min: 6, max: 20, message: 'Password should be 6 ～ 20 characters', trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        userName: [
          { validator: checkUserName, trigger: 'blur' }
        ],
        userPhone: [
          { validator: validatePhone, trigger: 'blur' }
        ],
      }
    };
  },
  methods: {
    register(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$axios.post('/login', {
            userName: this.registerForm.userName,
            password: this.registerForm.password,
            checkPass: this.registerForm.checkPass,
            userPhone:this.registerForm.userPhone
          })
              .then((response) => {
                if (response.status === 200) {
                  /*this.$store.commit('SET_TOKEN', response.data.token)//这行目前用不上
                  this.$store.commit('GET_USER', response.data.user)//这行目前用不上*/
                  this.$message({
                    message: 'Register successfully! You can login now',
                    type: 'success'
                  })
                  this.$router.push('/login')
                  alert(JSON.stringify(response.data))
                }
              })
              .catch(function (error) {
                console.log(error)
              })
        } else {
          console.log('error submit!!')
          return false
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style scoped>
.register{width: 460px;
  margin: auto;
  -webkit-border-radius: 28px;
  position: relative;}
</style>
